import React from 'react';
import Footer from './Footer.jsx';

export default class AboutPage extends React.Component {
  constructor () {
    super();
    this.state = {}
  }

  render () {
    return (
      <div className="about-page">
        <div className="about-banner">
          
          <div className="about-jdiu">
            <img src="imgs/jason.png" alt="Jason Diu cartoon" />
          </div>
        </div>
        <div className="cwidth">
          <div className="about-head">Hi, I'm Jason</div>
          <p className="intro-blurb">
            I'm a Software Developer and soon to be University of Waterloo Computer Science graduate just 
            beginning to explore the tech universe. I'm experienced in full stack web development, with an
            interest in machine learning and its applications. I also really like K-Pop.
          </p>

          <div className="about-head">My current to-do list:</div>
          <div className="todo">
            <div className="intro-point">
            <div className="icon-wrap"><i className="fa fa-bar-chart fa-2x"></i></div>
              <p>Successfully complete my Software Developer internship at <a href="http://www.nielsen.com/ca/en.html" className="ext-link" target="_blank">Nielsen</a></p>
            </div>
            <div className="intro-point">
              <div className="icon-wrap"><i className="fa fa-mortar-board fa-2x"></i></div>
              <p>Finish my Computer Science degree at the University of Waterloo</p>
            </div>
            <div className="intro-point">
            <div className="icon-wrap"><i className="fa fa-code fa-2x"></i></div>
              <p>Launch my first app, <a href="https://github.com/jdiu/fastmacros" className="ext-link" target="_blank">fastmacros</a>, a mobile app built using React Native</p>

            </div>
          </div>
        </div>
        <Footer />
      </div>
    )
  }
}


